import { BrowserRouter as Router, Routes, Route } from "react-router-dom";
import { Suspense } from "react"
import { lazy } from "react"
import axios from 'axios';
import i18n from "i18next";
import { useTranslation, initReactI18next } from "react-i18next";
import LanguageDetector from 'i18next-browser-languagedetector';
import HttpApi from 'i18next-http-backend';
import { MainProvider } from "./components/contexts/MainContext";
import Loader from "./components/Loader";

const Home = lazy(()=> import("./pages/home"));
const AboutUs = lazy(()=> import("./pages/about-us"));
const UsefulInformation = lazy(()=> import("./pages/useful-information"));
const Investment = lazy(()=> import("./pages/investment"));
const Rent = lazy(()=> import("./pages/property-status/for-rent"));
const Sale = lazy(()=> import("./pages/property-status/for-sale"));
const Overview = lazy(()=> import("./pages/overview"));
const E404page = lazy(()=> import("./pages/e404page"));
const Agent = lazy(()=> import("./pages/agent"));
const AdvancedSearch = lazy(()=> import("./pages/advanced"));
const Preview = lazy(()=> import("./pages/preview"));
const PropertyType = lazy(()=> import("./pages/property-type"));
const PropertyFeature = lazy(()=> import("./pages/property-feature"));
const Search = lazy(()=> import("./pages/search"));
const Author = lazy(()=> import("./pages/author"));
const Compare = lazy(()=> import("./pages/compare"));
const V360 = lazy(()=> import("./pages/v360"));
const E408page = lazy(()=> import("./pages/e408page"));

// import Home from "./pages/home";
// import AboutUs from "./pages/about-us";
// import UsefulInformation from "./pages/useful-information";
// import Investment from "./pages/investment";
// import Rent from "./pages/property-status/for-rent";
// import Sale from "./pages/property-status/for-sale";
// import Overview from "./pages/overview";
// import E404page from "./pages/e404page";
// import Agent from './pages/agent'
// import AdvancedSearch from "./pages/advanced";
// import Preview from "./pages/preview";
// import PropertyType from "./pages/property-type";
// import PropertyFeature from "./pages/property-feature";
// import Search from "./pages/search";
// import Author from "./pages/author";
// import Compare from "./pages/compare";
// import V360 from "./pages/v360";
// import E408page from "./pages/e408page";

const langs = ['en', 'ru', 'fa', 'tr', 'ar']

i18n
  .use(initReactI18next)
  .use(LanguageDetector)
  .use(HttpApi)
  .init({
    supportedLngs: langs,
    fallbackLng: "en",
    detection: {
      order: ['path', 'cookie', 'htmlTag'],
      caches: ['cookie']
    },
    backend: {
      loadPath: '/testView.github.io/locales/{{lng}}/translation.json',
    },
  });

const App = () => {

  // axios.defaults.baseURL = 'http://localhost:8005';

  axios.defaults.baseURL = 'https://api.syrysapp.com';

  axios.defaults.timeout = 300000;

  return (
    <Suspense fallback={<Loader />}>
    <MainProvider>
      <Router basename="/testView.github.io">
        <Routes>
          {[...langs, ''].map((lang) => (
            <>
              <Route path={`/${lang}/`} element={<Home />} />
              <Route path={`/${lang}/useful-information`} element={<UsefulInformation />} />
              <Route path={`/${lang}/property-status/for-sale`} element={<Sale />} />
              <Route path={`/${lang}/property-status/for-rent`} element={<Rent />} />
              <Route path={`/${lang}/property-type/:type`} element={<PropertyType />} />
              <Route path={`/${lang}/property-feature/:feature`} element={<PropertyFeature />} />
              <Route path={`/${lang}/investment`} element={<Investment />} />
              <Route path={`/${lang}/property/preview/:id`} element={<Preview />} />
              <Route path={`/${lang}/property/:id`} element={<Overview />} />
              <Route path={`/${lang}360/:id`} element={<V360 />} />
              <Route path={`/${lang}/about-us`} element={<AboutUs />} />
              <Route path={`/${lang}/agent/:user`} element={<Agent />} />
              <Route path={`/${lang}/author/syrysgmail-com`} element={<Author />} />
              <Route path={`/${lang}/search`} element={<Search />} />
              <Route path={`/${lang}/advanced-search`} element={<AdvancedSearch />} />
              <Route path={`/${lang}/compare`} element={<Compare />} />

              <Route path={`/${lang}/408`} element={<E408page />}/>
            </>
          ))}
          <Route path="*" element={<E404page />} />
        </Routes>
      </Router>
    </MainProvider>
    </Suspense>
  );
}

export default App;
